import * as React from "react";
import Box from "@mui/material/Box";
import Drawer from "@mui/material/Drawer";
import { Button, Grid, Stack, Typography } from "@mui/material";
import Slider from "@mui/material/Slider";
import CloseIcon from "@mui/icons-material/Close";
export default function SideBar({ open, setOpen }) {
  const [wallet, setWallet] = React.useState(true);
  const [utility, setUtility] = React.useState(false);
  const [calculator, setCalculator] = React.useState(false);
  const [info, setInfo] = React.useState(false);
  const [state, setState] = React.useState({
    top: false,
    left: false,
    bottom: false,
    right: false,
  });

  const toggleDrawer = (anchor, open) => (event) => {
    if (
      event.type === "keydown" &&
      (event.key === "Tab" || event.key === "Shift")
    ) {
      return;
    }

    setState({ ...state, [anchor]: open });
  };

  const list = (anchor) => (
    <Box
      width={{ xs: "300px", md: "400px" }}
      sx={{
        height: "100%",
        top: "100px",

        backgroundColor: "#1b1b1b",
        border: "2px solid #77828C",
        padding: "10px",
      }}
      role="presentation"
      onClick={toggleDrawer(anchor, false)}
      onKeyDown={toggleDrawer(anchor, false)}
    >
      <Stack direction="row" justifyContent="space-between" alignItems="center">
        <Button
          sx={{
            border: "2px solid #d7d0d8",
            padding: "3px 10px",
            borderRadius: "5px",
            color: "white",
            textTransform: "none",
          }}
        >
          <Typography fontSize={{ xs: "9px", sm: "12px", md: "14px" }}>
            Connect Wallet
          </Typography>
        </Button>
        <CloseIcon
          sx={{ color: "white", "&:hover": { cursor: "pointer" } }}
          onClick={() => setOpen(false)}
        />
      </Stack>
      {/* Tab section -- starts */}
      <Stack
        mt={2}
        direction="row"
        justifyContent="space-between"
        alignItems="center"
        border="2px solid #77828C"
        boxShadow="5px 5px #99A6B1"
        padding="5px"
      >
        <Typography
          onClick={() => {
            setCalculator(false);
            setUtility(false);
            setWallet(true);
            setInfo(false);
          }}
          padding="0px 2px"
          color="white"
          fontSize={{ xs: "9px", sm: "12px", md: "14px" }}
          sx={{
            "&:hover": { cursor: "pointer", borderBottom: "1px solid white" },
          }}
          border={wallet ? "1px solid white" : "none"}
        >
          WALLET
        </Typography>
        <Typography
          onClick={() => {
            setCalculator(false);
            setUtility(true);
            setWallet(false);
            setInfo(false);
          }}
          padding="0px 2px"
          color="white"
          fontSize={{ xs: "9px", sm: "12px", md: "14px" }}
          sx={{
            "&:hover": { cursor: "pointer", borderBottom: "1px solid white" },
          }}
          border={utility ? "1px solid white" : "none"}
        >
          UTILITY
        </Typography>
        <Typography
          onClick={() => {
            setCalculator(true);
            setUtility(false);
            setWallet(false);
            setInfo(false);
          }}
          padding="0px 2px"
          color="white"
          fontSize={{ xs: "9px", sm: "12px", md: "14px" }}
          sx={{
            "&:hover": { cursor: "pointer", borderBottom: "1px solid white" },
          }}
          border={calculator ? "1px solid white" : "none"}
        >
          CALCULATOR
        </Typography>
        <Typography
          onClick={() => {
            setCalculator(false);
            setUtility(false);
            setWallet(false);
            setInfo(true);
          }}
          padding="0px 2px"
          color="white"
          fontSize={{ xs: "9px", sm: "12px", md: "14px" }}
          sx={{
            "&:hover": { cursor: "pointer", borderBottom: "1px solid white" },
          }}
          border={info ? "1px solid white" : "none"}
        >
          INFO
        </Typography>
      </Stack>
      {/* {wallet ? <Wallet /> : null}
      {utility ? <Utility /> : null}
      {calculator ? <Calculator /> : null}
      {info ? <Info /> : null} */}
      {/* Tab section -- ends */}
      <Stack mt={3} justifyContent="center" alignItems="center" mb={1}>
        <Button
          sx={{
            color: "white",
            padding: "2px 15px",
            border: "1px solid white",
            borderRadius: "5px",
          }}
        >
          Disconnect
        </Button>
      </Stack>
    </Box>
  );

  return (
    <div>
      <Drawer
        anchor={"right"}
        open={open}
        onClose={() => setOpen(false)}
        sx={{ mt: "100px" }}
      >
        {list("right")}
      </Drawer>
    </div>
  );
}
