import { Avatar, Box, Container, Grid, Stack } from "@mui/material";
import React, { useContext, useEffect, useState } from "react";
import WestIcon from "@mui/icons-material/West";
import { Link, useParams } from "react-router-dom";
import CheckCircleIcon from "@mui/icons-material/CheckCircle";
import VoteModal from "./VoteModal";
import axios from "axios";
import { ownerSponsor, url } from "../Connectivity/Environment";
import { AppContext } from "../utils/utils";
import { ToastNotify, useDaoContract } from "../Connectivity/Hooks";
import Loading from "./loading";
import moment from "moment";

export default function DaoVotesDetail() {
  const [open, setOpen] = useState(false);
  const [state, setstate] = useState("For");
  const [image, setimage] = useState("");
  const { account, signer } = useContext(AppContext);
  const daoContract = useDaoContract(signer);
  const [memberData, setmemberData] = useState({});
  const [daoMembers, setdaoMembers] = useState([ownerSponsor]);
  const [forVotes, setforVotes] = useState(0);
  const [againstVotes, setagainstVotes] = useState(0);
  const [loading, setloading] = useState(true);
  const [alertState, setAlertState] = useState({
    open: false,
    message: "",
    severity: undefined,
  });
  const { id } = useParams();

  const getDaoMember = async () => {
    try {
      setloading(true);
      const { data } = await axios.get(url + `dao/get-member-byId?id=${id}`);
      console.log(data, "data");
      if (data) {
        let [{ forVotes: forV, againstVotes: againstV }] = await Promise.all([
          daoContract.candidancyProposals(data?.account),
        ]);
        let resultForVotes = 0;
        let resultAgainstVotes = 0;
        if (+forV > 0) {
          resultForVotes = (+forV / (+forV + +againstV)) * 100;
        }
        if (+againstV > 0) {
          resultAgainstVotes = (+againstV / (+forV + +againstV)) * 100;
        }
        setforVotes(resultForVotes.toFixed(2));
        setagainstVotes(resultAgainstVotes.toFixed(2));

        setmemberData(data);
      }
      setloading(false);
    } catch (error) {
      console.log(error);
    }
  };
  const getDaoMembers = async () => {
    try {
      const { data } = await axios.get(url + `dao/get-dao-member`);
      setdaoMembers([...daoMembers, ...data]);
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => {
    getDaoMembers();
    getDaoMember();
  }, [id]);
  const signHandler = async () => {
    try {
      setloading(true);
      let tx = await daoContract.signAsSponsor(account);
      await tx.wait();
      let { data } = await axios.get(url + `dao/sign-dao-member?id=${id}`);
      getDaoMember();
      setloading(false);
      setAlertState({
        open: true,
        message: "transaction successfull",
        severity: "success",
      });
    } catch (error) {
      console.log("Error uploading file: ", error);
      if (error?.data?.message) {
        setAlertState({
          open: true,
          message: error?.data?.message,
          severity: "error",
        });
      } else if (error?.reason) {
        setAlertState({
          open: true,
          message: error?.reason,
          severity: "error",
        });
      } else {
        setAlertState({
          open: true,
          message: error?.message,
          severity: "error",
        });
      }
      setloading(false);
    }
  };
  const voteHandler = async () => {
    try {
      setloading(true);
      let vote = state === "For" ? true : false;
      let tx = await daoContract.voteToCandidancyProposal(vote, account);
      await tx.wait();
      getDaoMember();
      setloading(false);
      setAlertState({
        open: true,
        message: "transaction successfull",
        severity: "success",
      });
    } catch (error) {
      console.log("Error uploading file: ", error);
      if (error?.data?.message) {
        setAlertState({
          open: true,
          message: error?.data?.message,
          severity: "error",
        });
      } else if (error?.reason) {
        setAlertState({
          open: true,
          message: error?.reason,
          severity: "error",
        });
      } else {
        setAlertState({
          open: true,
          message: error?.message,
          severity: "error",
        });
      }
      setloading(false);
    }
  };
  return (
    <>
      <ToastNotify alertState={alertState} setAlertState={setAlertState} />
      <Loading loading={loading} />
      <Box>
        <VoteModal
          open={open}
          setOpen={setOpen}
          state={state}
          voteHandler={voteHandler}
        />
        <Container maxWidth="lg">
          <Grid container mt={5}>
            <Grid item xs={12} sm={8} md={8} spacing={5} color="#000">
              <Link to="/" style={{ color: "#fff", textDecoration: "none" }}>
                <Stack
                  fontSize="16px"
                  fontWeight={500}
                  mb={5}
                  color="gray"
                  direction="row"
                  spacing={2}
                  alignItems="center"
                >
                  <WestIcon style={{ marginRight: "5px" }} /> Back
                </Stack>
              </Link>
              <Stack
                direction="row"
                // justifyContent="space-between"
                padding="20px"
                mt={5}
                fontWeight={600}
                borderRadius="25px"
              >
                <Avatar
                  src={url + memberData?.image}
                  sx={{ width: 128, height: 128, mx: "20px" }}
                />
                <Box fontSize="16px">
                  {/* <Stack direction="row" justifyContent="space-between">
                  </Stack> */}
                  <Box fontSize="16px" fontWeight={900} my={1}>
                    DAO member candiate
                  </Box>

                  <Box fontSize="16px">
                    {memberData?.name} - {memberData?.jobRole} -{" "}
                    {memberData?.webName}{" "}
                  </Box>
                  <Box
                    my={1}
                    width="max-content"
                    fontSize="14px"
                    bgcolor={memberData?.active ? "green" : "red"}
                    color="#fff"
                    fontWeight={500}
                    padding="5px 20px"
                    alignSelf="center"
                    borderRadius="25px"
                  >
                    {memberData?.active ? "Active" : "Closed"}
                  </Box>

                  <Box
                    style={{
                      display: "flex",
                      alignItems: "center",
                      border: "1px solid lightgray",
                      borderRadius: "25px",
                      padding: "5px 15px",
                      width: "max-content",
                    }}
                  >
                    {memberData?.sponsorName} {"   "}
                    <CheckCircleIcon
                      style={{ color: memberData.signed ? "green" : "gray" }}
                    />
                  </Box>
                </Box>
              </Stack>
              <Box fontSize="16px" width="90%" ml="auto" fontWeight={600}>
                <Box>{memberData?.description}</Box>
                <Box mt={2}>Phone : {memberData?.phoneNumber}</Box>
                <Box> {memberData?.webLink}</Box>
                <Box>{memberData?.email}</Box>
                <Box mt={2}>{memberData?.webName}</Box>
                <Box>Address : {memberData?.address}</Box>
              </Box>
            </Grid>
            <Grid item xs={12} sm={4} md={4} color="gray" mt={{ xs: 5, sm: 0 }}>
              <Box border="1px solid lightgray" borderRadius="15px">
                <Box
                  color="#000"
                  fontSize="16px"
                  fontWeight={700}
                  padding="15px 20px"
                  borderBottom="1px solid lightgray"
                >
                  Create your vote
                </Box>
                <Box fontSize="14px" fontWeight={500} padding="15px 20px">
                  <Box
                    padding="10px 20px"
                    textAlign="center "
                    borderRadius="15px"
                    color="#000"
                    border={
                      state === "For"
                        ? "1px solid green"
                        : "1px solid lightgray"
                    }
                    sx={{ cursor: "pointer" }}
                    onClick={() => {
                      setstate("For");
                    }}
                  >
                    For
                  </Box>
                  <Box
                    mt={1}
                    padding="10px 20px"
                    textAlign="center "
                    border={
                      state === "Against"
                        ? "1px solid green"
                        : "1px solid lightgray"
                    }
                    borderRadius="15px"
                    color="#000"
                    onClick={() => {
                      setstate("Against");
                    }}
                    sx={{ cursor: "pointer" }}
                  >
                    Against
                  </Box>
                  {account?.toLowerCase() ===
                    memberData?.sponsorAddress?.toLowerCase() && (
                    <Box
                      mt={1}
                      padding="10px 20px"
                      textAlign="center "
                      border="1px solid lightgray"
                      borderRadius="15px"
                      fontWeight={600}
                      bgcolor="blue"
                      color="#fff"
                      sx={{ cursor: "pointer" }}
                      onClick={signHandler}
                    >
                      Sign sponsership
                    </Box>
                  )}
                  <Box
                    mt={1}
                    padding="10px 20px"
                    textAlign="center "
                    border="1px solid lightgray"
                    borderRadius="15px"
                    fontWeight={600}
                    bgcolor="blue"
                    color="#fff"
                    onClick={() => {
                      setOpen(true);
                    }}
                    sx={{ cursor: "pointer" }}
                  >
                    Vote
                  </Box>
                </Box>
              </Box>
              <Box border="1px solid lightgray" borderRadius="15px" mt={4}>
                <Box
                  color="#000"
                  fontSize="16px"
                  fontWeight={700}
                  padding="15px 20px"
                  borderBottom="1px solid lightgray"
                >
                  Result
                </Box>
                <Box fontSize="14px" fontWeight={500} padding="15px 20px">
                  <Stack direction="row" justifyContent="space-between">
                    <Box>For</Box>
                    <Box>{forVotes}%</Box>
                  </Stack>
                  <Box
                    width="100%"
                    height="10px"
                    borderRadius="25px"
                    bgcolor="lightgray"
                    overflow="hidden"
                  >
                    <Box
                      width={`${forVotes}%`}
                      height="100%"
                      bgcolor="blue"
                    ></Box>
                  </Box>
                  <Stack direction="row" justifyContent="space-between" mt={2}>
                    <Box>against</Box>
                    <Box>{againstVotes}%</Box>
                  </Stack>
                  <Box
                    width="100%"
                    height="10px"
                    borderRadius="25px"
                    bgcolor="lightgray"
                    overflow="hidden"
                  >
                    <Box
                      width={`${againstVotes}%`}
                      height="100%"
                      bgcolor="blue"
                    ></Box>
                  </Box>
                  <Stack direction="row" justifyContent="space-between" mt={3}>
                    <Box>Start date</Box>
                    <Box>{moment(memberData.startAt).format("lll")}</Box>
                  </Stack>
                  <Stack direction="row" justifyContent="space-between" mt={1}>
                    <Box>End date</Box>
                    <Box>
                      {moment(memberData.startAt).add("day", 2).format("lll")}
                    </Box>
                  </Stack>
                </Box>
              </Box>
            </Grid>
          </Grid>
        </Container>
      </Box>
    </>
  );
}
