import React, { useEffect, useRef, useState } from "react";
import { Avatar, Box, Container, Menu, MenuItem, Stack } from "@mui/material";
import { Link } from "react-router-dom";
import SearchIcon from "@mui/icons-material/Search";
import PersonIcon from "@mui/icons-material/Person";
import KeyboardArrowDownIcon from "@mui/icons-material/KeyboardArrowDown";
import { url } from "../Connectivity/Environment";
import axios from "axios";
import DaoVotesCards from "./daoVotesCards";
import Loading from "./loading";

export default function Cards() {
  const [anchorEl, setAnchorEl] = React.useState(null);
  const [daoMembers, setdaoMembers] = useState([]);
  const [allData, setallData] = useState([]);
  const [rejectedMembers, setrejectedMembers] = useState([]);
  const [activeData, setactiveData] = useState([]);
  const [closedData, setclosedData] = useState([]);
  const [loading, setloading] = useState(false);
  const [choice, setchoice] = useState("All Votes");
  const open = Boolean(anchorEl);
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  // input ref
  const ref = useRef(null);
  const handleclick = () => {
    ref.current.focus();
  };

  const getDaoMembers = async () => {
    try {
      setloading(true);
      const { data } = await axios.get(url + `dao/get-dao-members`);
      const { data: data1 } = await axios.get(url + `dao/get-all`);
      let canceled = data1.filter((item) => !item.daoMember && !item.active);
      let active = data1.filter((item) => item.active);
      let closed = data1.filter((item) => !item.active);

      setrejectedMembers(canceled);
      setactiveData(active);
      setclosedData(closed);
      setallData(data1);
      setdaoMembers(data);
      setloading(false);
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => {
    getDaoMembers();
  }, []);
  return (
    <>
      <Loading loading={loading} />
      <Box my={5}>
        <Container maxWidth="md">
          <Stack
            direction={{ xs: "column-reverse", sm: "row" }}
            justifyContent="space-between"
            color="#000"
          >
            {/*menu  */}
            <Box
              id="demo-positioned-button"
              aria-controls={open ? "demo-positioned-menu" : undefined}
              aria-haspopup="true"
              aria-expanded={open ? "true" : undefined}
              onClick={handleClick}
              display="flex"
              alignItems="center"
              color="#000"
              fontSize="20px"
              p={2}
              sx={{ cursor: "pointer" }}
            >
              <Box mx={1.5} fontWeight={700}>
                {choice}
              </Box>
              <KeyboardArrowDownIcon />
            </Box>
            <Menu
              id="demo-positioned-menu"
              aria-labelledby="demo-positioned-button"
              anchorEl={anchorEl}
              open={open}
              onClose={handleClose}
              anchorOrigin={{
                vertical: "bottom",
                horizontal: "center",
              }}
              transformOrigin={{
                vertical: "top",
                horizontal: "center",
              }}
              MenuListProps={{
                sx: {
                  width: "150px",
                  cursor: "pointer",
                  border: "1px solid #eee",
                  color: "#000",
                  "& .MuiMenuItem-root": {
                    padding: 1.5,
                    borderBottom: "1px solid #eee",
                  },
                },
              }}
            >
              <MenuItem
                onClick={() => {
                  setchoice("All Votes");
                  handleClose();
                }}
              >
                All Votes
              </MenuItem>
              <MenuItem
                onClick={() => {
                  setchoice("Active Votes");
                  handleClose();
                }}
              >
                Active Votes
              </MenuItem>
              <MenuItem
                onClick={() => {
                  setchoice("Closed Votes");
                  handleClose();
                }}
              >
                Closed Votes
              </MenuItem>
              <MenuItem
                onClick={() => {
                  setchoice("DAO Members");
                  handleClose();
                }}
              >
                DAO Members
              </MenuItem>{" "}
              <MenuItem
                onClick={() => {
                  setchoice("Refused Members");
                  handleClose();
                }}
              >
                Refused Members
              </MenuItem>
            </Menu>

            {/*  input*/}
            <Box
              display="flex"
              alignItems="center"
              padding="10px 10px"
              border="1px solid lightgray "
              borderRadius="5px"
              onClick={handleclick}
            >
              <SearchIcon />
              <input ref={ref} style={{ border: "none" }} />
            </Box>
          </Stack>
          {choice === "All Votes" ? (
            <DaoVotesCards data={allData} />
          ) : choice === "Active Votes" ? (
            <DaoVotesCards data={activeData} />
          ) : choice === "Closed Votes" ? (
            <DaoVotesCards data={closedData} />
          ) : choice === "DAO Members" ? (
            <DaoVotesCards data={daoMembers} />
          ) : (
            choice === "Refused Members" && (
              <DaoVotesCards data={rejectedMembers} />
            )
          )}
        </Container>
      </Box>
    </>
  );
}
