import React, { useState, useContext } from "react";
import {
  Box,
  Container,
  useMediaQuery,
  Grid,
  Hidden,
  Menu,
  MenuItem,
} from "@mui/material";
import MenuIcon from "@mui/icons-material/Menu";
import { AppContext } from "../utils/utils";
import SwipeableDrawer from "@mui/material/SwipeableDrawer";
import Button from "@mui/material/Button";
import List from "@mui/material/List";
import ListItem from "@mui/material/ListItem";
import ListItemText from "@mui/material/ListItemText";
import { makeStyles } from "@mui/styles";
import clsx from "clsx";
import Avatar from "@mui/material/Avatar";
import PersonIcon from "@mui/icons-material/Person";
import { Paper } from "@mui/material";
import KeyboardArrowDownIcon from "@mui/icons-material/KeyboardArrowDown";
import WalletModal from "./WalletModal";
import { Link } from "react-router-dom";

const useStyles = makeStyles({
  list: {
    width: 250,
    paddingTop: "70px",
  },
  fullList: {
    width: "auto",
    alignItems: "center",
  },
  paper: {
    background: "#fff !important",
    justifyContent: "space-between",
  },
  // hover: {
  //   "&:hover": {
  //     color: "#9EFF00",
  //   },
  // },
});

function Navbar({ setWalletOpen }) {
  const classes = useStyles();
  const [state, setState] = useState(false);
  const { account, connect, disconnect } = useContext(AppContext);
  const matches = useMediaQuery("(max-width:960px)");
  const [openModal, setopenModal] = useState(false);
  const [anchorEl, setAnchorEl] = React.useState(null);
  const open = Boolean(anchorEl);
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };
  const handleWalletOpen = () => {
    handleClose();
    setWalletOpen(true);
  };

  const toggleDrawer = (anchor, open) => (event) => {
    if (
      event &&
      event.type === "keydown" &&
      (event.key === "Tab" || event.key === "Shift")
    ) {
      return;
    }
    setState({ ...state, [anchor]: open });
  };
  const list = (anchor) => (
    <div
      className={clsx(classes.list, {
        [classes.fullList]: anchor === "top" || anchor === "bottom",
      })}
      role="presentation"
      onClick={toggleDrawer(anchor, false)}
      onKeyDown={toggleDrawer(anchor, false)}
    >
      <Box mb={5} display="flex" justifyContent="center">
        <Box
          fontFamily="Roboto"
          fontWeight={700}
          fontSize={matches ? "25px" : "40px"}
          color="#000"
        >
          TheArtwork
        </Box>
      </Box>
      <List>
        {[
          <a
            // href="https://hazecrypto.net/audit/avaxfortune"
            href="/main"
            target="_blank"
            style={{
              textDecoration: "none",
              color: "#000",
            }}
          >
            <Link to="/" style={{ color: "#fff", textDecoration: "none" }}>
              <Box
                sx={{
                  "&:Hover": {
                    color: "#9EFF00",
                  },
                }}
              >
                DAO Votes
              </Box>
            </Link>
          </a>,
        ].map((text, index) => (
          <ListItem
            button
            style={{
              justifyContent: "center",
            }}
            key={text}
          >
            <ListItemText
              className={classes.hover}
              style={{
                textTransform: "capitalize",
                textAlign: "center",
                textDecoration: "none",
                cursor: "pointer",
                color: "#ffffff",
                fontSize: "15px",
                fontWeight: 600,
              }}
              primary={text}
            />
          </ListItem>
        ))}
      </List>
      <Box mt={5} display="flex" justifyContent="center">
        {account ? (
          <Box
            bgcolor="#000"
            width="80px"
            height="40px"
            fontWeight="500"
            borderRadius="50px"
            fontSize="12px"
            color="#fff"
            display="flex"
            justifyContent="center"
            alignItems="center"
            letterSpacing="1%"
            onClick={() => disconnect()}
            style={{ zIndex: 1 }}
          >
            {account.slice(0, 4) + "..." + account.slice(-4)}
          </Box>
        ) : (
          <Box
            zIndex={1}
            bgcolor="#000"
            width="100px"
            height="40px"
            fontWeight="500"
            borderRadius="50px"
            fontSize="12px"
            color="#fff"
            display="flex"
            justifyContent="center"
            alignItems="center"
            letterSpacing="1%"
            onClick={() => setopenModal(true)}
          >
            Join DAO
          </Box>
        )}
      </Box>
    </div>
  );

  const logout = () => {};
  return (
    <Box position="relative">
      <WalletModal setopenModal={setopenModal} openModal={openModal} />
      <Box
        bgcolor="#FFF"
        borderBottom="1px solid #727272"
        height="100px"
        display="flex"
        justifyContent="center"
        alignItems="center"
      >
        <Container maxWidth="lg">
          <Box
            display="flex"
            justifyContent="space-between"
            alignItems="center"
          >
            <Box
              display="flex"
              justifyContent="space-between"
              alignItems="center"
              flexBasis={matches ? "100%" : "27%"}
            >
              <Grid
                container
                direction="row"
                justifyContent="start"
                alignItems="center"
              >
                <Grid item>
                  <Box
                    fontFamily="Roboto"
                    fontWeight={700}
                    fontSize={matches ? "25px" : "40px"}
                    color="#000"
                    sx={{ cursor: "pointer", textDecoration: "none" }}
                    component={Link}
                    to="/"
                  >
                    TheArtwork
                  </Box>
                </Grid>
              </Grid>
            </Box>

            <Box
              display={{ xs: "none", sm: "block" }}
              flexBasis={matches ? "30%" : "23%"}
            >
              <Hidden mdDown>
                {account ? (
                  <Box
                    width="100%"
                    fontWeight={700}
                    display="flex"
                    alignItems="center"
                    justifyContent="flex-end"
                  >
                    <Box
                      id="demo-positioned-button"
                      aria-controls={open ? "demo-positioned-menu" : undefined}
                      aria-haspopup="true"
                      aria-expanded={open ? "true" : undefined}
                      onClick={handleClick}
                      display="flex"
                      alignItems="center"
                      color="#000"
                      fontSize="20px"
                      p={2}
                      sx={{ cursor: "pointer" }}
                    >
                      <Avatar sx={{ bgcolor: "#000" }}>
                        <PersonIcon />
                      </Avatar>
                      {"  "}
                      <Box mx={1.5} fontWeight={700}>
                        Account
                      </Box>
                      <KeyboardArrowDownIcon />
                    </Box>
                    <Menu
                      id="demo-positioned-menu"
                      aria-labelledby="demo-positioned-button"
                      anchorEl={anchorEl}
                      open={open}
                      onClose={handleClose}
                      anchorOrigin={{
                        vertical: "bottom",
                        horizontal: "center",
                      }}
                      transformOrigin={{
                        vertical: "top",
                        horizontal: "center",
                      }}
                      MenuListProps={{
                        sx: {
                          width: "150px",
                          cursor: "pointer",
                          border: "1px solid #eee",
                          color: "#000",
                          "& .MuiMenuItem-root": {
                            padding: 2,
                            borderBottom: "1px solid #eee",
                          },
                        },
                      }}
                    >
                      {/* <MenuItem onClick={handleWalletOpen}>My Wallet</MenuItem> */}

                      <Link
                        to="/"
                        style={{ color: "#000", textDecoration: "none" }}
                      >
                        <MenuItem onClick={handleClose}>DAO votes</MenuItem>
                      </Link>
                      <Link
                        to="/profile"
                        style={{ color: "#000", textDecoration: "none" }}
                      >
                        <MenuItem onClick={handleClose}>My profile</MenuItem>
                      </Link>
                      <MenuItem onClick={disconnect}>Log Out</MenuItem>
                    </Menu>
                  </Box>
                ) : (
                  <Box
                    width="100%"
                    fontWeight={700}
                    display="flex"
                    alignItems="center"
                    justifyContent="space-between"
                  >
                    <Box
                      color="#000"
                      component={Link}
                      to="/"
                      sx={{ cursor: "pointer", textDecoration: "none" }}
                    >
                      DAO Votes
                    </Box>
                    <Box
                      display="flex"
                      justifyContent="space-evenly"
                      alignItems="center"
                    >
                      <Box
                        zIndex={1}
                        bgcolor="#000"
                        ml={1}
                        width="100px"
                        height="40px"
                        fontWeight="500"
                        borderRadius="50px"
                        fontSize="12px"
                        color="#fff"
                        display="flex"
                        justifyContent="center"
                        alignItems="center"
                        sx={{ cursor: "pointer" }}
                        letterSpacing="1%"
                        onClick={() => setopenModal(true)}
                      >
                        Join DAO
                      </Box>
                    </Box>
                  </Box>
                )}
              </Hidden>
            </Box>
            <Hidden mdUp>
              {["left"].map((anchor) => (
                <React.Fragment key={anchor}>
                  <Button
                    onClick={toggleDrawer(anchor, true)}
                    style={{ zIndex: 1, justifyContent: "end", width: "100%" }}
                  >
                    <MenuIcon
                      style={{
                        fontSize: "38px",
                        cursor: "pointer",
                        color: "#000",
                      }}
                    ></MenuIcon>
                  </Button>
                  <Paper style={{ background: "#fff" }}>
                    <SwipeableDrawer
                      classes={{ paper: classes.paper }}
                      anchor={anchor}
                      open={state[anchor]}
                      onClose={toggleDrawer(anchor, false)}
                      onOpen={toggleDrawer(anchor, true)}
                    >
                      {list(anchor)}
                    </SwipeableDrawer>
                  </Paper>
                </React.Fragment>
              ))}
            </Hidden>
          </Box>
        </Container>
      </Box>
    </Box>
  );
}

export default Navbar;
