import * as React from "react";
import {
  Box,
  Dialog,
  DialogContent,
  Modal,
  Stack,
  useMediaQuery,
} from "@mui/material";
import { useTheme } from "@emotion/react";

const style = {
  color: "#000",
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: 400,
  bgcolor: "background.paper",
  border: "5px solid lightgray",
  outline: "none",
  borderRadius: "10px",
  // boxShadow: 24,
  p: 4,
};

export default function VoteModal({ open, setOpen, state, voteHandler }) {
  const handleClose = () => setOpen(false);
  const theme = useTheme();

  const fullScreen = useMediaQuery(theme.breakpoints.down("md"));

  return (
    <>
      <Dialog
        open={open}
        onClose={handleClose}
        fullScreen={fullScreen}
        maxWidth="md"
        PaperProps={{
          sx: {
            bgcolor: "white",
            border: "5px solid gray",
          },
        }}
      >
        <DialogContent sx={{ color: "black" }}>
          <Box fontSize="16px" fontWeight={700} textAlign="center">
            Are you sure you want to cast this vote?
            <br />
            This action cannot be undone.
          </Box>
          <Box
            sx={{
              my: 3,
              fontSize: "24px",
              fontWeight: 700,
              border: "1px solid lightgray ",
              borderRadius: "10px",
              textAlign: "center",
              padding: "20px 15px",
            }}
          >
            {state}
          </Box>
          <Stack
            direction="row"
            justifyContent="space-evenly"
            py={2}
            style={{ borderTop: "1px solid lightgray" }}
          >
            <Box
              padding="8px 35px"
              border="1px solid lightgray"
              borderRadius="25px"
              onClick={handleClose}
              sx={{ cursor: "pointer" }}
            >
              {" "}
              Cancel
            </Box>
            <Box
              padding="8px 35px"
              bgcolor="blue"
              color="#fff"
              borderRadius="25px"
              sx={{
                cursor: "pointer",
              }}
              onClick={() => {
                handleClose();
                voteHandler();
              }}
            >
              Vote
            </Box>
          </Stack>
        </DialogContent>
      </Dialog>
    </>
  );
}
