import React, { useContext, useState } from "react";
import {
  Avatar,
  Box,
  Button,
  Container,
  Grid,
  IconButton,
  Menu,
  MenuItem,
  Stack,
  TextField,
} from "@mui/material";
import KeyboardArrowDownIcon from "@mui/icons-material/KeyboardArrowDown";
import { Link } from "react-router-dom";
import MemberModal from "./MemberModal";
import { PhotoCamera } from "@mui/icons-material";
import { useEffect } from "react";
import { AppContext, gasEstimationForAll } from "../utils/utils";
import axios from "axios";
import { ownerSponsor, url } from "../Connectivity/Environment";
import Loading from "./loading";
import { ToastNotify, useDaoContract } from "../Connectivity/Hooks";

function Profile() {
  const [Open, setOpen] = useState(false);
  const [anchorEl, setAnchorEl] = useState(null);
  const [anchorEl1, setAnchorEl1] = useState(null);
  const [file, setfile] = useState("");
  const { account, signer } = useContext(AppContext);
  const daoContract = useDaoContract(signer);
  const [webName, setwebName] = useState("WhiteHouse Gallery");
  const [name, setname] = useState("");
  const [webLink, setwebLink] = useState("Whitehousegallery.com");
  const [phoneNumber, setphoneNumber] = useState("31425367489506");
  const [address, setaddress] = useState("White House Gallery 178 avenue");
  const [email, setemail] = useState("Contact@WhiteHouse Gallery");
  const [description, setdescription] = useState(
    "Lorem ipsum dolor sit amet consectetur adipisicing elit. Earum quam corporis delectus. Inventore possimus perferendis molestias, provident porro cum aspernatur ad, est odio adipisci id harum, fugiat architecto at deleniti!"
  );
  const [jobRole, setjobRole] = useState("Gallery Director");
  const [sponsorName, setsponsorName] = useState("");
  const [sponsorAddress, setsponsorAddress] = useState("");
  const [data, setdata] = useState(null);
  const [daoMembers, setdaoMembers] = useState([ownerSponsor]);
  const [alertState, setAlertState] = useState({
    open: false,
    message: "",
    severity: undefined,
  });
  const [loading, setloading] = useState(false);

  const open = Boolean(anchorEl);
  const open1 = Boolean(anchorEl1);
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };
  const handleClick1 = (event) => {
    setAnchorEl1(event.currentTarget);
  };

  const handleClose1 = () => {
    setAnchorEl1(null);
  };

  const fileChangeHandler = async (e) => {
    console.log(e.target.files[0]);
    var image = e.target.files[0];
    var reader = new FileReader();
    reader.onloadend = function () {
      let obj = {
        base64: reader.result,
        file: image,
      };
      setfile(obj);
    };
    reader.readAsDataURL(image);
  };
  const init = async () => {
    const { data } = await axios.get(url + `dao/get-member?account=${account}`);
    console.log(data, "data");
    setdata(data);
    if (data) {
      setwebName(data.webName);
      setname(data.name);
      setwebLink(data.webLink);
      setphoneNumber(data.phoneNumber);
      setaddress(data.address);
      setemail(data.email);
      setdescription(data.description);
      setjobRole(data.jobRole);
      setsponsorName(data.sponsorName);
      setsponsorAddress(data.sponsorAddress);
    }
  };
  const getDaoMembers = async () => {
    const { data } = await axios.get(url + `dao/get-dao-member`);
    setdaoMembers([...daoMembers, ...data]);
  };
  console.log(daoMembers);

  useEffect(() => {
    if (account) {
      init();
    }
    getDaoMembers();
  }, [account]);

  const submitData = async () => {
    if (!data) {
      if (!sponsorName) {
        setAlertState({
          open: true,
          message: "Please select your sponsor",
          severity: "error",
        });
      } else if (!file.file) {
        setAlertState({
          open: true,
          message: "Please select your profile picture",
          severity: "error",
        });
      } else {
        try {
          setloading(true);
          let fn = daoContract.estimateGas.saveCandidancyProposal;
          let param = [];
          const tx = await daoContract.saveCandidancyProposal({
            gasLimit: gasEstimationForAll(account, fn, param),
          });
          await tx.wait();
          let allData = {
            name,
            webName,
            account,
            webLink,
            phoneNumber,
            address,
            email,
            description,
            jobRole,
            sponsorName,
            sponsorAddress,
            image: file.file,
          };
          const dataToSubmit = new FormData();
          for (var key in allData) {
            dataToSubmit.append(key, allData[key]);
          }
          let { data } = await axios.post(
            url + `dao/save-member`,
            dataToSubmit
          );
          if (data.success) {
            setAlertState({
              open: true,
              message: data.message,
              severity: "success",
            });
            setloading(false);
            init();
            getDaoMembers();
          } else {
            setAlertState({
              open: true,
              message: data.message,
              severity: "error",
            });
            setloading(false);
          }
        } catch (error) {
          console.log("Error uploading file: ", error);
          if (error?.data?.message) {
            setAlertState({
              open: true,
              message: error?.data?.message,
              severity: "error",
            });
          } else if (error?.reason) {
            setAlertState({
              open: true,
              message: error?.reason,
              severity: "error",
            });
          } else {
            setAlertState({
              open: true,
              message: error?.message,
              severity: "error",
            });
          }
          setloading(false);
        }
      }
    } else {
      try {
        setloading(true);
        // let fn = daoContract.estimateGas.saveCandidancyProposal;
        // let param = [];
        // const tx = await daoContract.saveCandidancyProposal({
        //   gasLimit: gasEstimationForAll(account, fn, param),
        // });
        // await tx.wait();
        let allData = {
          id: data._id,
          name,
          webName,
          account,
          webLink,
          phoneNumber,
          address,
          email,
          description,
          jobRole,
          sponsorName,
          sponsorAddress,
          image: file.file,
          file: file.file ? "" : data.image,
        };
        const dataToSubmit = new FormData();
        for (var key in allData) {
          dataToSubmit.append(key, allData[key]);
        }
        let { data: data1 } = await axios.post(
          url + `dao/edit-member`,
          dataToSubmit
        );
        if (data1.success) {
          setAlertState({
            open: true,
            message: data1.message,
            severity: "success",
          });
          setloading(false);
          init();
          getDaoMembers();
        } else {
          setAlertState({
            open: true,
            message: data1.message,
            severity: "error",
          });
          setloading(false);
        }
      } catch (error) {
        console.log("Error uploading file: ", error);
        if (error?.data?.message) {
          setAlertState({
            open: true,
            message: error?.data?.message,
            severity: "error",
          });
        } else if (error?.reason) {
          setAlertState({
            open: true,
            message: error?.reason,
            severity: "error",
          });
        } else {
          setAlertState({
            open: true,
            message: error?.message,
            severity: "error",
          });
        }
        setloading(false);
      }
    }
  };
  console.log(data, "data");

  return (
    <>
      <ToastNotify alertState={alertState} setAlertState={setAlertState} />
      <Loading loading={loading} />
      <MemberModal
        open={Open}
        setOpen={setOpen}
        data={{
          name,
          webName,
          webLink,
          phoneNumber,
          address,
          email,
          description,
          jobRole,
          sponsorName,
          sponsorAddress,
          file,
          image: data?.image,
          signed: data?.signed,
        }}
      />
      <Box py={8}>
        <Container maxWidth="lg">
          <Box fontSize="20px" fontWeight={600} color="#000" mb={7}>
            My Profile
          </Box>
          <Grid container spacing={2} justifyContent="center">
            <Grid item xs={12} sm={6} md={3}>
              <Stack spacing={2} alignItems="center">
                <Box fontSize="20px" fontWeight={600} color="#000" mb={2}>
                  {data ? (
                    name
                  ) : (
                    <input
                      placeholder="Name"
                      style={{
                        fontSize: { xs: "12px", sm: "16px" },
                        fontWeight: "600",
                        borderRadius: "50px",
                        border: "2px solid #727272",
                      }}
                      value={name}
                      onChange={(e) => setname(e.target.value)}
                    />
                  )}
                </Box>

                {data?.image && !file?.file ? (
                  <>
                    <Avatar
                      src={url + data?.image}
                      sx={{ width: "120px", height: "120px" }}
                    >
                      U
                    </Avatar>
                    <IconButton
                      color="primary"
                      aria-label="upload picture"
                      component="label"
                      sx={{
                        bgcolor: "black",
                        "&:hover": {
                          bgcolor: "black",
                        },
                      }}
                    >
                      <input
                        hidden
                        accept="image/*"
                        type="file"
                        onChange={fileChangeHandler}
                      />
                      <PhotoCamera />
                    </IconButton>
                  </>
                ) : (
                  <>
                    <Avatar
                      src={file?.base64}
                      sx={{ width: "120px", height: "120px" }}
                    >
                      U
                    </Avatar>
                    <IconButton
                      color="primary"
                      aria-label="upload picture"
                      component="label"
                      sx={{
                        bgcolor: "black",
                        "&:hover": {
                          bgcolor: "black",
                        },
                      }}
                    >
                      <input
                        hidden
                        accept="image/*"
                        type="file"
                        onChange={fileChangeHandler}
                      />
                      <PhotoCamera />
                    </IconButton>
                  </>
                )}
              </Stack>
            </Grid>
            <Grid item xs={12} sm={6} md={2.5}>
              <Stack spacing={2} alignItems="center" pt={4}>
                <input
                  placeholder="WhiteHouse Gallery"
                  style={{
                    marginBottom: "10px",
                    fontSize: { xs: "12px", sm: "16px" },
                    fontWeight: "600",
                    borderRadius: "50px",
                    border: "2px solid #727272",
                  }}
                  value={webName}
                  onChange={(e) => setwebName(e.target.value)}
                />
                <input
                  placeholder="31425367489506"
                  style={{
                    marginBottom: "10px",
                    fontSize: { xs: "12px", sm: "16px" },
                    fontWeight: "600",
                    borderRadius: "50px",
                    border: "2px solid #727272",
                  }}
                  value={phoneNumber}
                  onChange={(e) => setphoneNumber(e.target.value)}
                />
                <input
                  placeholder="Contact@WhiteHouse Gallery"
                  style={{
                    marginBottom: "10px",
                    fontSize: { xs: "12px", sm: "16px" },
                    fontWeight: "600",
                    borderRadius: "50px",
                    border: "2px solid #727272",
                  }}
                  value={email}
                  onChange={(e) => setemail(e.target.value)}
                />
                <Box
                  mb={2}
                  id="demo-positioned-button"
                  aria-controls={open ? "demo-positioned-menu" : undefined}
                  aria-haspopup="true"
                  aria-expanded={open ? "true" : undefined}
                  onClick={handleClick}
                  display="flex"
                  alignItems="center"
                  color="#000"
                  fontSize="20px"
                  sx={{
                    cursor: "pointer",
                  }}
                >
                  <Box mr={2} fontWeight={700}>
                    {jobRole}
                  </Box>
                  <KeyboardArrowDownIcon />
                </Box>
                <Menu
                  id="demo-positioned-menu"
                  aria-labelledby="demo-positioned-button"
                  anchorEl={anchorEl}
                  open={open}
                  onClose={handleClose}
                  anchorOrigin={{
                    vertical: "bottom",
                    horizontal: "center",
                  }}
                  transformOrigin={{
                    vertical: "top",
                    horizontal: "center",
                  }}
                  MenuListProps={{
                    sx: {
                      cursor: "pointer",
                      border: "1px solid #eee",
                      color: "#000",
                      "& .MuiMenuItem-root": {
                        borderBottom: "1px solid #eee",
                      },
                    },
                  }}
                >
                  <MenuItem
                    onClick={() => {
                      setjobRole("Gallery Director");
                      handleClose();
                    }}
                  >
                    Gallery Director
                  </MenuItem>

                  <MenuItem
                    onClick={() => {
                      setjobRole("Shop Keeper");
                      handleClose();
                    }}
                  >
                    Shop Keeper
                  </MenuItem>

                  <MenuItem
                    onClick={() => {
                      setjobRole("Merchant");
                      handleClose();
                    }}
                  >
                    Merchant
                  </MenuItem>
                </Menu>
              </Stack>
            </Grid>
            <Grid item xs={12} sm={6} md={3}>
              <Stack spacing={2} alignItems="center" pt={4}>
                <input
                  placeholder="Whitehousegallery.com"
                  style={{
                    marginBottom: "10px",
                    fontSize: { xs: "12px", sm: "16px" },
                    fontWeight: "600",
                    borderRadius: "50px",
                    border: "2px solid #727272",
                  }}
                  value={webLink}
                  onChange={(e) => setwebLink(e.target.value)}
                />
                <input
                  placeholder="White House Gallery 178 avenue"
                  style={{
                    marginBottom: "10px",
                    fontSize: { xs: "12px", sm: "16px" },
                    fontWeight: "600",
                    borderRadius: "50px",
                    border: "2px solid #727272",
                  }}
                  value={address}
                  onChange={(e) => setaddress(e.target.value)}
                />
              </Stack>
            </Grid>
            <Grid item xs={12} sm={6} md={3.5}>
              <Stack spacing={2} alignItems="center" pt={4}>
                <textarea
                  id="w3review"
                  name="w3review"
                  placeholder="Write a Description"
                  rows="10"
                  cols="30"
                  style={{
                    padding: "10px",
                    fontWeight: "600",
                    borderRadius: "15px",
                    border: "2px solid #727272",
                  }}
                  value={description}
                  onChange={(e) => setdescription(e.target.value)}
                ></textarea>
              </Stack>
            </Grid>
          </Grid>
          <Box
            my={4}
            display="flex"
            flexDirection={{ xs: "column", sm: "row" }}
            justifyContent="space-between"
            alignItems="center"
          >
            <Box
              display="flex"
              flexDirection={{ xs: "column", sm: "row" }}
              justifyContent="space-between"
              alignItems="center"
              flexBasis={{ xs: "100%", sm: "35%" }}
            >
              <Box fontSize="20px" fontWeight={600} color="#000" mb={2}>
                Sponsored by:
              </Box>
              {data ? (
                <input
                  placeholder="WhiteHouse Gallery"
                  style={{
                    mb: 2,
                    fontSize: { xs: "12px", sm: "16px" },
                    fontWeight: "600",
                    borderRadius: "50px",
                    border: "2px solid #727272",
                  }}
                  readOnly
                  value={sponsorName}
                />
              ) : (
                <>
                  <Box
                    mb={2}
                    id="demo-positioned-button"
                    aria-controls={open1 ? "demo-positioned-menu" : undefined}
                    aria-haspopup="true"
                    aria-expanded={open1 ? "true" : undefined}
                    onClick={handleClick1}
                    display="flex"
                    alignItems="center"
                    color="#000"
                    fontSize="20px"
                    sx={{
                      cursor: "pointer",
                    }}
                  >
                    <Box mr={2} fontWeight={700}>
                      {sponsorName ? sponsorName : "Select Sponsor"}
                    </Box>
                    <KeyboardArrowDownIcon />
                  </Box>
                  <Menu
                    id="demo-positioned-menu"
                    aria-labelledby="demo-positioned-button"
                    anchorEl={anchorEl1}
                    open={open1}
                    onClose={handleClose1}
                    anchorOrigin={{
                      vertical: "bottom",
                      horizontal: "center",
                    }}
                    transformOrigin={{
                      vertical: "top",
                      horizontal: "center",
                    }}
                    MenuListProps={{
                      sx: {
                        cursor: "pointer",
                        border: "1px solid #eee",
                        color: "#000",
                        "& .MuiMenuItem-root": {
                          borderBottom: "1px solid #eee",
                        },
                      },
                    }}
                  >
                    {daoMembers.map(({ name, address }) => (
                      <MenuItem
                        onClick={() => {
                          setsponsorName(name);
                          setsponsorAddress(address);
                          handleClose1();
                        }}
                      >
                        {name}
                      </MenuItem>
                    ))}
                  </Menu>
                </>
              )}
            </Box>
            <Box
              style={{ cursor: "pointer" }}
              my={2}
              fontSize={{ xs: "12px", sm: "16px" }}
              fontWeight={600}
              color="#000"
              padding="8px"
              borderRadius="50px"
              border="2px solid #727272"
              px={1}
              width="max-content"
              onClick={() => {
                setOpen(true);
              }}
            >
              DAO member view
            </Box>
          </Box>
          <Stack spacing={2} alignItems="center">
            <Button
              variant="contained"
              sx={{
                color: "#fff",
                backgroundColor: "#000",
                borderRadius: "50px",
                width: "140px",
                height: "40px",
                fontWeight: 700,
              }}
              onClick={submitData}
            >
              Save
            </Button>
          </Stack>
        </Container>
      </Box>
    </>
  );
}

export default Profile;
