import React, { useContext } from "react";
import { Dialog, DialogContent, Box, Slide } from "@mui/material";
import Avatar from "@mui/material/Avatar";
import PersonIcon from "@mui/icons-material/Person";
import metamask from "../assets/metamask.png";
import { AppContext } from "../utils/utils";

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});
function WalletModal({ setopenModal, openModal }) {
  const { account, connect, disconnect } = useContext(AppContext);

  const handleClose = () => {
    setopenModal(false);
  };

  return (
    <div>
      <div className="modal__main__container">
        <Dialog
          open={openModal}
          keepMounted
          TransitionComponent={Transition}
          onClose={handleClose}
          aria-labelledby="alert-dialog-slide-title"
          aria-describedby="alert-dialog-slide-description"
        >
          <DialogContent
            className="dialoge__content__section"
            style={{
              border: "2px solid",
              borderImage:
                "linear-gradient(90deg, #eee -1.72%, #727272 100%) 1",
              background: "#fff",
              boxShadow: "0px 0px 13px rgba(182, 0, 211, 0.7)",
              width: "500px",
            }}
          >
            <Box
              display="flex"
              alignItems="center"
              color="#000"
              fontSize="20px"
              borderBottom="1px solid #eee"
              p={2}
            >
              <Avatar>
                <PersonIcon />
              </Avatar>
              {"  "}
              <Box ml={2} fontWeight={700}>
                Connect Wallet
              </Box>
            </Box>

            <Box
              mt={7}
              display="flex"
              alignItems="center"
              color="#000"
              fontSize="16px"
              border="1px solid #eee"
              borderRadius="8px"
              p={2}
              sx={{ cursor: "pointer" }}
              onClick={() => {
                connect();
                handleClose();
              }}
            >
              <img src={metamask} width="30px" alt="" />
              <Box ml={2} fontWeight={700}>
                MetaMask
              </Box>
            </Box>
          </DialogContent>
        </Dialog>
        {/* </Slide> */}
      </div>
    </div>
  );
}

export default WalletModal;
