import React, { useContext, useEffect, useState } from "react";
import Web3 from "web3";
import { AppContext } from "./utils/utils";
import Navbar from "./components/Navbar";
import Footer from "./components/Footer";
import NetworkChange from "./utils/NetworkModal";
import DaoVotes from "./components/daoVotes";
import DaoVotesDetail from "./components/daoVotesDetails";
import WalletModal from "./components/WalletModal";
import { Route, Routes } from "react-router-dom";
import Profile from "./components/Profile";
import MyWallet from "./components/MyWallet";
import VoteModal from "./components/VoteModal";
import SideBar from "./components/sidebar";

function App() {
  const { account, signer, connect, chainId } = useContext(AppContext);
  const [switchNetwork, setswitchNetwork] = useState(false);
  const [walletOpen, setWalletOpen] = useState(false);

  useEffect(() => {
    if (!account) {
      connect();
    }
    if (window.location.href.includes("?ref=")) {
      let getAddress = window.location.href.split("?ref=")[1];
      console.log(getAddress, "getAddress");
      let final = getAddress.slice(0, 42);
      localStorage.setItem("REF_ADDRESS", final);
    }
  }, [account]);

  return (
    <>
      {/* <NetworkChange open={switchNetwork} setOpen={setswitchNetwork} /> */}
      <SideBar open={walletOpen} setOpen={setWalletOpen} />
      <Navbar setWalletOpen={setWalletOpen} />
      <MyWallet />
      <Routes>
        <Route exact path="/" element={<DaoVotes />} />
        <Route path="/profile" element={<Profile />} />
        <Route path="/votes-details/:id" element={<DaoVotesDetail />} />
      </Routes>
      <Footer />
    </>
  );
}

export default App;
